import { Car } from "@/model/Car";
import { User } from "@/model/User";
import { FirebaseDBService } from "@/services/FirebaseDBService";
import { UserService } from "@/services/UserService";
import { getMessaging, onMessage } from "firebase/messaging";
import { Module } from "vuex";
import { RootState } from "..";
import { LISTEN_MESSAGES } from "./messagesModule";

export interface AppStateVuex {
    cars: Car[] | null;
    user: User | null;
    installEvent: (Event & { prompt: () => void }) | null;
}

const firebase = new FirebaseDBService();

export const appStateModule: Module<AppStateVuex, RootState> = {
    state: {
        cars: null,
        user: null,
        installEvent: null
    },
    mutations: {
        updateCars(state, cars) {
            state.cars = cars;
        },
        updateUser(state, user) {
            state.user = { ...user };
        },
        setInstallEvent(state, installEvent) {
            state.installEvent = installEvent;
        },
    },
    actions: {
        listenCars: ({ commit }) => {
            firebase.listenForChanges<{ [id: string]: Car }>('cars', (cars) => commit('updateCars', Object.values(cars)));
        },
        listenUser: async ({ commit, rootState, dispatch }) => {
            const user = await firebase.readData<User>(`users/${rootState.firebase.user?.uid}`);
            if (user) {
                commit('updateUser', user);
                const userService = new UserService();
                const currentToken = await userService.getMessagingToken();
                dispatch(LISTEN_MESSAGES);
                if (currentToken && !(user.notificationTokens && Object.keys(user.notificationTokens).includes(currentToken))) {
                    const uid = rootState.firebase.user?.uid;
                    if (uid) {
                        await userService.saveNoticationToken(currentToken, uid);
                    }
                }
            }

        }
    },
    getters: {
        carByPlate: (state) => (plate: string) => {
            if (state.cars) {
                return state.cars.find(c => c.plate === plate);
            }
            return null;
        },
        usedCar: (state) => {
            if (state.cars && state.user) {
                return state.cars.find(c => c.plate === state.user?.car);
            }
            return null;
        },
        permissions: (state) => {
            if (state.user) {
                return Object.keys(state.user.permissions);
            }
            return [];
        },
        notificationTokens: (state) => {
            if (state.user && state.user.notificationTokens) {
                return Object.keys(state.user.notificationTokens);
            }
            return [];
        },
    }
}

export const
    LISTEN_CARS = 'listenCars',
    LISTEN_USER = 'listenUser',
    SET_INSTALL_EVENT = 'setInstallEvent'